body{
  background: #fafafa !important;
  transition: 0.5s;

}


.App{
 
  animation: fade 1s alternate forwards;
  animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}

@keyframes fade {
  0%{
    opacity: 0;
    transform: scale(0.8) ;
  }
  100% {
      transform: scale(1) ;
      opacity: 1;
  }
}





.margintop{
  margin-top: 5rem;
  color:#0278ae;
}
.kart{

  width: 20rem;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 0px;
  background: white;
  border: 1px solid lightgray;
  margin: 30px;
  transition: 0.2s linear;

}


.shrink {
  animation: shrink 500ms alternate-reverse ;
  animation-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
}


@keyframes shrink {
  100% {
      transform: scale(0.9, 0.9);
  }
}


.kart:hover{
  background: #0278ae;
  cursor: pointer;
  transform: scale(1.1) skewY(0deg) ;
  box-shadow: 0px 0px 5px white;

}
.kart:hover h4{
  color: white!important;

}
.kart:hover span{
  color: white!important;
  animation: _3d alternate 500ms forwards; 
}
.kart:hover svg{
  color:white!important;
  animation: _3d alternate 500ms forwards; 
}


@keyframes _3d {
  0% {
    transform: scale(1);
}
  100% {
      transform: scale(1.1);
  }
}


.kart h4{
  color: #0278ae;
  margin-top: 2rem;
}
.kart span{
  font-size: 50px;
  color: #0278ae;
  }
  
.kart svg{
font-size: 50px;
color: #0278ae;
}

